export default function ServiceDetailPage({ params }: { params: { serviceId: string } }) {
  const service = services.find((s) => s.id === params.serviceId);
  if (!service) return notFound();
  const Icon = service.icon;
  return (
    <div className="min-h-screen bg-[#f5f5f5] py-16">
      <div className="max-w-[900px] mx-auto px-4">
        <div className="text-center mb-12">
          <Icon className="w-16 h-16 mx-auto mb-6 text-[#c4b59a]" />
          <h1 className="text-5xl font-extrabold text-[#3a3a3a] mb-6">{service.title}</h1>
          <p className="text-xl text-[#666666] max-w-2xl mx-auto">{service.description}</p>
        </div>
        {/* Features */}
        <div className="mb-12">
          <h2 className="text-3xl font-extrabold text-[#3a3a3a] mb-6 text-center">Key Features</h2>
          <div className="grid md:grid-cols-3 gap-4">
            {service.features.map((feature, idx) => (
              <div key={idx} className="bg-white p-6 rounded shadow text-center">
                <p className="text-[#3a3a3a] font-medium">{feature}</p>
              </div>
            ))}
          </div>
        </div>
        {/* Example Projects */}
        <div className="mb-12">
          <h2 className="text-3xl font-extrabold text-[#3a3a3a] mb-6 text-center">Example Projects</h2>
          <div className="grid md:grid-cols-3 gap-8">
            {service.projects.map((project, idx) => (
              <Card key={idx} className="border-none">
                <div className="relative h-48 bg-slate-200 rounded-t overflow-hidden">
                  <div className="absolute inset-0 bg-gradient-to-br from-slate-300 to-slate-400" />
                  <div className="absolute bottom-4 left-4 right-4 text-white">
                    <h4 className="text-lg font-bold">{project.name}</h4>
                    <p className="text-sm opacity-90">{project.location}</p>
                  </div>
                </div>
                <CardContent className="pt-4">
                  <p className="text-[#666666]">{project.description}</p>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
        {/* Process */}
        <div>
          <h2 className="text-3xl font-extrabold text-[#3a3a3a] mb-6 text-center">Our Process</h2>
          <div className="grid md:grid-cols-4 gap-8">
            {service.process.map((step) => (
              <div key={step.step} className="text-center">
                <div className="w-16 h-16 bg-[#c4b59a] text-white rounded-full flex items-center justify-center text-2xl font-bold mx-auto mb-4">
                  {step.step}
                </div>
                <h4 className="text-xl font-bold text-[#3a3a3a] mb-2">{step.title}</h4>
                <p className="text-[#666666]">{step.description}</p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}